<template>
    <div style="height: 100%">
        <div class="bank-account-row">
            <div class="bank-account-index">
                <img src="../../assets/img/default.png">
                <span>本人账户转账</span>
                <span class="account-title-dest">实时转账</span>
            </div>
        </div>
        <div  class="bank-account-row">
            <a class="bank-account-index" href="#/accounts/internal-transfer/index">
                <img src="../../assets/img/default.png">
                <span>行内转账</span>
                <span class="account-title-dest">支持账户、手机号、二维码转账</span>
            </a>
        </div>
        <div  class="bank-account-row">
            <div class="bank-account-index">
                <img src="../../assets/img/default.png">
                <span>行外转账</span>
                <span class="account-title-dest">5万元以下支持实时转账</span>
            </div>
        </div>
        <div  class="bank-account-row">
            <div class="bank-account-index">
                <img src="../../assets/img/default.png">
                <span>农信银转账</span>
                <span class="account-title-dest">省内外农信银协议银行互转</span>
            </div>
        </div>
        <div  class="bank-account-row">
            <div class="bank-account-index">
                <img src="../../assets/img/default.png">
                <span>定向大额转账</span>
                <span class="account-title-dest">签约账户定向大额转账</span>
            </div>
        </div>
        <div  class="bank-account-row">
            <div class="bank-account-index">
                <img src="../../assets/img/default.png">
                <span>自助设备转账撤销</span>
                <span class="account-title-dest">签约账户定向大额转账</span>
            </div>
        </div>
        <div class="recent-transfer">
            <span style="height: 60px;line-height: 60px;">最近转账：</span>
            <div class="list">
                <cell class="item" v-for="item in recentTransferList" 
                    :title=" item.name " :label=" item.account " :key=" item.id " to="detail" >
                    <img class="cell-icon" slot="icon" src="../../assets/5289.png" >
                </cell>
            </div>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/accounts';

export default {
    data() {
        return {
            recentTransferList: []
        };
    },
    created() {
        service.getAccountsIndex().then((res) => {
            this.recentTransferList = res.data.data;
        }, (err) => {
            console.log( err );
        });
    },
    computed: {
    },
    components: {
    },
    methods: {
    }
};
</script>


<style lang="scss">
.bank-account-row{
    float: left;
    width: 50%;
    height: 20%;
    background: #fff;
    border: solid 1px #a9acb1;
    .bank-account-index {
        position: absolute;
        padding-left: 5%;
        padding-top: 10%;
        img {
            position: absolute;
        }
        span {
            display: block;
            width: 320px;
            padding-left: 110px;
        }
        .account-title-dest {
            color: #b8bbbf;
        }
    }
}
.list {
    .item {
        img {
            width: 80px;
            height: 80px;
        }
    }
}
.recent-transfer {
    background: #FBFBFB;
    position: absolute;
    width: 100%;
    bottom: 0px;
}
</style>



