<template>
    <div class="list">
        <cell class="item" v-for="item in accountList" 
            title="交易时间" label="交易金额" :value="item.dateTime" :desc="item.money" :key=" item.id " 
            :to="{ path: 'detail', query: { id: item.id } }"  is-link>
        </cell>
    </div>
</template>

<script>


import * as service from '@/services/accounts';

export default {
    data() {
        return {
          accountList: []
        };
    },
    created() {
      this.accountList = Object.assign({}, this.$store.state.deviceRevocation.search).data;
      if(this.accountList.length == 0) {
        this.accountList = [];
        var id = this.$route.query.id || '';
        service.getDeviceRevocationList().then((res) => {
            this.accountList = res.data.data;
        }, (err) => {
            console.log( err );
        });
      }
    },
    computed: {
    },
    components: {
    },
    methods: {
      toDetail(id) {
        var params = {
          path: '/device-revocation/detail',
          query: {
            id: id
          }
        };
        this.$router.push(params);
      }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName']){
            var account = Object.assign({}, this.$store.state.deviceRevocation.search).account;
            this.$store.dispatch('updateDeviceRevocationSearch', {account: account, data: this.accountList} );
        }else{
            this.$store.dispatch('clearDeviceRevocationSearch');
        }
        next( true );
    }
};
</script>


<style lang="scss">
    .list
    {
        .item
        {
            .cell-label,.cell-desc
            {
                font-size: 13px!important;
                color: #333!important;
            }
        }
    }
</style>



