<template>
    <div class="device-revocation-row">
        <span style="display: block;padding-bottom: 20px;">交易信息确认:</span>
        <div class="device-revocation-detail">
            <div class="detail-items">
                <label>转出账户：</label>
                <span>{{accountsDetail.accounts}}</span>
            </div>
            <div class="detail-items">
                <label>转入账户：</label>
                <span>10000000001</span>
            </div>
            <div class="detail-items">
                <label>转入方户名：</label>
                <span>kanade</span>
            </div>
            <div class="detail-items">
                <label>转账类型：</label>
                <span>{{accountsDetail.accountsType}}</span>
            </div>
            <div class="detail-items">
                <label>交易金额：</label>
                <span>100.00</span>
            </div>
            <div class="detail-items">
                <label>交易日期：</label>
                <span>{{accountsDetail.date}}</span>
            </div>
            <div class="detail-items">
                <label>交易时间：</label>
                <span>{{accountsDetail.dateTime}}</span>
            </div>
            <div class="detail-items">
                <label>自助转账状态：</label>
                <span>未撤销</span>
            </div>
        </div>
        <div class="device-revocation-detail detail-items-li">
            <div class="detail-items">
                <cell-password :options=" cellPassword " ></cell-password>
            </div>
        </div>
        <div class="field-action">
            <mt-button size="large" type="primary">确定</mt-button>
        </div>
    </div>
</template>

<script>


import * as service from '@/services/accounts';

export default {
    data() {
        return {
            accountsDetail: [],
            cellPassword: {
                title: '交易密码',
                rule: 'number' // number or mix
            }
        };
    },
    created() {
      var id = this.$route.query.id || '';
      service.getDeviceRevocationDetail().then((res) => {
          this.accountsDetail = res.data;
      }, (err) => {
          console.log( err );
      });
    },
    computed: {
    },
    components: {
    },
    methods: {
    }
};
</script>


<style lang="scss">
.device-revocation-row {
    padding: 40px;
    .device-revocation-detail {
        background: #fff;
        border: 1px solid #ddd;
        border-radius: 20px;
          .detail-items {
            padding: 20px;
            border-bottom: 1px solid #ddd;
                label {
                    display: inline-block;
                    width: 40%;
                    height: 60px;
                    line-height: 60px;
                }
          }
    }
}
.detail-items-li {
    margin-top: 20px;
}

</style>



