import ajax from './ajax';
import api from './api';

export function getAccountsIndex(data) {
    return ajax.post( api.getAccountsIndex, data );
}

export function getDeviceRevocationIndex(data) {
    return ajax.post( api.getDeviceRevocationIndex, data );
}

export function getDeviceRevocationList(data) {
    return ajax.post( api.getDeviceRevocationList, data );
}

export function getDeviceRevocationDetail(data) {
    return ajax.post( api.getDeviceRevocationDetail, data );
}



