<template>
	<div class="transaction-detail">
		<div class="field-part">
            <cell class="item"
                title="付款账户" :value="dataForm.account" :desc="dataForm.showMoney"
                @click.native="showSelectorPaymentAccount()"  is-link>
            </cell>
            <mt-field label="转账金额" placeholder="请输入转账金额" v-model="dataForm.money"></mt-field>
		</div>
        <div class="field-part my-payee-img">
            <mt-field label="收款人" placeholder="请输入收款人" v-model="dataForm.payee"></mt-field>
            <mt-field label="收款账户" placeholder="请输入收款账户" v-model="dataForm.payeeAccount"></mt-field>
            <img src="../../../assets/img/bank/payee.png" @click="showSelectorTransferAging()">
        </div>
        <span style="height: 90px;line-height: 90px;padding-right: 20px;text-align: end;display: block;">实时到账，<label style="color: #26a2ff;">更换到账时间</label></span>
		<div class="field-action">
			<mt-button @click.native="search()" type="primary">下一步</mt-button>
		</div>
		<selector-payment-account ref="selectorPaymentAccount" :options=" selectorPaymentAccount " ></selector-payment-account>
        <selector-transfer-aging ref="selectorTransferAging" :options=" selectorTransferAging " ></selector-transfer-aging>
	</div>
</template>

<script>

import { Toast } from 'mint-ui-rem-dpr';
import { mapGetters, mapActions, mapState } from 'vuex';

import * as service from '@/services/payment';

export default {
    data() {
        return {
            dataForm: {
                account: '',
                showMoney: '',
                money: '',
                payee: '',
                payeeAccount: '',
                remark: ''
            },
            selectorPaymentAccount: {
                title: '选择付款账号',
                checked: {},
                showAddBtn: false,
                accountList: []
            },
            selectorTransferAging: {
                title: '选择到账时间',
                checked: '',
                instant: true,
                general: true,
                morrow: true
            }
        };
    },
    created(){
        this.dataForm.account = Object.assign({}, this.$store.state.deviceRevocation.search).account;
        service.getAccountList().then((res) => {
            this.dataForm.account = res.data[0].account;
            this.dataForm.showMoney = '可用余额：' + res.data[0].account;
            this.selectorPaymentAccount.accountList = res.data;
        }, (err) => {
            console.log( err );
        });
    },
    mounted(){
    },
    methods: {
        search () {
            let _data = Object.assign({}, this.dataForm );
            var _error = [];
            if( this.$validate.isEmpty( _data.account ) ) {
                _error.push( '银行卡号不能为空。' );
            }
            if( _error.length > 0 ) {
                Toast({
                    message: _error[0],
                    iconClass: 'error',
                    duration: 1000
                });
                return;
            }
    		var params = {
    			path: '/device-revocation/list',
    			query: {
    				id: _data.account
    			}
    		};
            this.$router.push(params);
        },
        showSelectorPaymentAccount() {
            this.$refs.selectorPaymentAccount.show().then(( data ) => {
                this.selectorPaymentAccount.checked = data.item;
                this.dataForm.account = data.item.account;
                this.dataForm.showMoney = '可用余额：' + data.item.account;
            }, ( data ) => {});
        },
        showSelectorTransferAging() {
            this.$refs.selectorTransferAging.show().then(( data ) => {
                this.selectorTransferAging.checked = data.item.code;
                console.log(data);
            }, ( data ) => {});
        }
    },
    beforeRouteLeave (to, from, next) {
        if( to.meta['groupName'] === from.meta['groupName'] || to.meta['groupName'] == 'selector' ){
            this.$store.dispatch('updateDeviceRevocationSearch', this.dataForm );
        }else{
            this.$store.dispatch('clearDeviceRevocationSearch');
        }
        next( true );
    }
};
</script>

<style lang="scss">
.my-payee-img {
    position: relative;
    img {
        width: 80px;
        position: absolute;
        top: 55px;
        right: 10px;
    }
}
</style>



